import { Container } from 'react-bootstrap';

const AboutUs = () => (
    <div className='contact'>
      <h1 style={{fontSize:'60px',paddingBottom:'40px'}}>About U<span style={{color:'#FF8C00'}}>s</span></h1>
      <p style={{backgroundColor:'#FF8C00',fontSize:'30px',textAlign:'left',paddingLeft:'50px',paddingRight:'50px'}}>
      "India is emerging as a land of numerous opportunities due to its economic advancement.
       The demand for skilled human resource is increasing at a rapid pace across every sector of the industry. 
       However, educational practices aren't able to deliver ready to employ human resources. The perception of the student community 
       is the root cause for the gap between industry and academics. After years of research and numerous trails, 
       SPANAV has found an innovative solution for bridging the gap between industry requirement and academics. SPANAV is an E-Learning platform
     where students can grow through us by unlocking their hidden potential and achieving various milestones of their career path. 
      Our innovative training methodologies improve student's employability skills by gamification of education which fosters fun-way of learning."
      </p>
      <h1 style={{fontSize:'60px',paddingBottom:'40px'}}>OUR MISSIO<span style={{color:'#FF8C00'}}>N</span></h1>
      <Container>
          <p style={{fontSize:'24px',textAlign:'left'}}>Our motto is to transform every student and make them industry-ready graduates and to do so we have devised a five-phase mechanism:</p>
        <ul style={{listStyleType:'square',textAlign:'left'}}>
            <li>Assessment Phase:
        <p>    Students will take up the psychometric assessment. A detailed report based on their test is generated. </p>
            </li>
            <li>Unlocking Phase:
            <p>  Our expert career counsellors will help the students in unlocking their hidden potentials.</p>
            </li>
            <li>Experimental Phase:
            <p>   Our experienced mentors will facilitate the students with regular sessions where mentors will foster learn while experimenting mechanism.
</p>
            </li>
            <li>Exploration Phase:
            <p>    Students can interact with the community of industrialists via weekly webinars and exciting events. </p>
            </li>
            <li>Implementation Phase:
            <p>  After completion of all the milestones in their career path, students will be presented with internship opportunities in various startups and companies in their field of study. </p>
            </li>
        
        </ul>
      </Container>

    </div>
  );
  export default AboutUs